:root {
  --background-color: #fafbfc;
  --surface-color: #ffffff;
  --btn-color: #5e72e4;
  --btn-hover-color: #4c63d2;
  --btn-text-color: #fff;
  --accent-color: #00f5ff;
  --success-color: #2dce89;
  --warning-color: #fb6340;
  --error-color: #f5365c;
  --project-card-bg: #ffffff;
  --card-bg: #ffffff;
  --card-border: rgba(0, 0, 0, 0.04);
  --text-color: #0f1419;
  --text-secondary: #536471;
  --form-text-color: #495057;
  --footer-text-color: #8898aa;
  --transition-duration: 0.4s;
  --transition-ease: cubic-bezier(0.25, 0.8, 0.25, 1);
  --border-radius-sm: 10px;
  --border-radius: 16px;
  --border-radius-lg: 24px;
  --border-radius-xl: 32px;
  --shadow-xs: 0 1px 3px rgba(0, 0, 0, 0.02);
  --shadow-sm: 0 4px 6px -1px rgba(0, 0, 0, 0.04), 0 2px 4px -1px rgba(0, 0, 0, 0.03);
  --shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.06), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --shadow-lg: 0 20px 35px -10px rgba(0, 0, 0, 0.08), 0 10px 20px -5px rgba(0, 0, 0, 0.04);
  --shadow-xl: 0 30px 60px -15px rgba(0, 0, 0, 0.12), 0 20px 40px -10px rgba(0, 0, 0, 0.06);
  --shadow-card: 0 0 0 1px rgba(0, 0, 0, 0.02), 0 5px 10px rgba(0, 0, 0, 0.03), 0 15px 40px rgba(0, 0, 0, 0.04);
  --shadow-hover: 0 0 0 1px rgba(94, 114, 228, 0.1), 0 10px 20px rgba(94, 114, 228, 0.08), 0 20px 60px rgba(94, 114, 228, 0.06);
  --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --gradient-accent: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  --gradient-surface: linear-gradient(135deg, #fafbfc 0%, #f0f2f5 100%);
  --gradient-mesh: radial-gradient(at 40% 20%, hsla(248, 79%, 70%, 0.2) 0px, transparent 50%),
    radial-gradient(at 80% 0%, hsla(189, 100%, 56%, 0.2) 0px, transparent 50%),
    radial-gradient(at 0% 50%, hsla(355, 100%, 93%, 0.15) 0px, transparent 50%),
    radial-gradient(at 80% 50%, hsla(340, 100%, 76%, 0.15) 0px, transparent 50%),
    radial-gradient(at 0% 100%, hsla(22, 100%, 77%, 0.15) 0px, transparent 50%),
    radial-gradient(at 80% 100%, hsla(242, 100%, 70%, 0.2) 0px, transparent 50%),
    radial-gradient(at 0% 0%, hsla(343, 100%, 76%, 0.15) 0px, transparent 50%);
  --glass-bg: rgba(255, 255, 255, 0.85);
  --glass-border: rgba(255, 255, 255, 0.3);
  --spacing-xs: 4px;
  --spacing-sm: 8px;
  --spacing-md: 16px;
  --spacing-lg: 24px;
  --spacing-xl: 32px;
  --spacing-2xl: 48px;
  --spacing-3xl: 64px;
  --animation-bounce: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  --animation-smooth: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  --animation-spring: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  --glow-primary: 0 0 40px rgba(94, 114, 228, 0.3);
  --glow-accent: 0 0 40px rgba(0, 245, 255, 0.3);
}

[data-theme='dark'] {
  --background-color: #0d1117;
  --surface-color: #161b22;
  --btn-color: #58a6ff;
  --btn-hover-color: #79c0ff;
  --btn-text-color: #0d1117;
  --accent-color: #39d353;
  --success-color: #3fb950;
  --warning-color: #d29922;
  --error-color: #f85149;
  --project-card-bg: #161b22;
  --card-bg: #161b22;
  --card-border: rgba(255, 255, 255, 0.06);
  --text-color: #f0f6fc;
  --text-secondary: #8b949e;
  --form-text-color: #c9d1d9;
  --footer-text-color: #8b949e;
  --shadow-xs: 0 1px 3px rgba(0, 0, 0, 0.4);
  --shadow-sm: 0 4px 6px -1px rgba(0, 0, 0, 0.5), 0 2px 4px -1px rgba(0, 0, 0, 0.4);
  --shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.6), 0 10px 10px -5px rgba(0, 0, 0, 0.4);
  --shadow-lg: 0 20px 35px -10px rgba(0, 0, 0, 0.7), 0 10px 20px -5px rgba(0, 0, 0, 0.5);
  --shadow-xl: 0 30px 60px -15px rgba(0, 0, 0, 0.8), 0 20px 40px -10px rgba(0, 0, 0, 0.6);
  --shadow-card: 0 0 0 1px rgba(255, 255, 255, 0.05), 0 5px 10px rgba(0, 0, 0, 0.3), 0 15px 40px rgba(0, 0, 0, 0.4);
  --shadow-hover: 0 0 0 1px rgba(88, 166, 255, 0.2), 0 10px 20px rgba(88, 166, 255, 0.15), 0 20px 60px rgba(88, 166, 255, 0.1);
  --gradient-primary: linear-gradient(135deg, #58a6ff 0%, #79c0ff 100%);
  --gradient-accent: linear-gradient(135deg, #39d353 0%, #3fb950 100%);
  --gradient-surface: linear-gradient(135deg, #0d1117 0%, #161b22 100%);
  --glass-bg: rgba(22, 27, 34, 0.85);
  --glass-border: rgba(255, 255, 255, 0.1);
  --glow-primary: 0 0 40px rgba(88, 166, 255, 0.4);
  --glow-accent: 0 0 40px rgba(57, 211, 83, 0.4);
}

* {
  box-sizing: border-box;
}

body {
  background: var(--gradient-surface);
  display: flex;
  justify-content: center;
  margin: 0;
  padding-bottom: 60px;
  transition: all var(--transition-duration) var(--transition-ease);
  scroll-behavior: smooth;
  line-height: 1.6;
  font-feature-settings: "liga" 1, "kern" 1;
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--gradient-mesh);
  opacity: 0.4;
  pointer-events: none;
  z-index: 0;
  animation: gradientShift 20s ease infinite;
}

@keyframes gradientShift {
  0%, 100% {
    transform: translate(0, 0) rotate(0deg) scale(1);
    filter: hue-rotate(0deg) blur(0px);
  }
  25% {
    transform: translate(-30px, -40px) rotate(90deg) scale(1.1);
    filter: hue-rotate(90deg) blur(1px);
  }
  50% {
    transform: translate(20px, -20px) rotate(180deg) scale(0.95);
    filter: hue-rotate(180deg) blur(2px);
  }
  75% {
    transform: translate(-10px, 30px) rotate(270deg) scale(1.05);
    filter: hue-rotate(270deg) blur(1px);
  }
}

#root {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-3xl);
  width: 100%;
  max-width: 1200px;
  padding: 0 var(--spacing-lg);
  transition: all var(--transition-duration) var(--transition-ease);
  position: relative;
  z-index: 1;
}

h1,
h2,
h3,
h4,
p {
  color: var(--text-color);
  margin: 0;
  padding: 0;
  transition: color var(--transition-duration) ease;
}

h2,
h3,
h4,
p {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2 {
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

h2,
h3 {
  font-size: 20px;
  font-weight: 600;
}

h1 {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: clamp(32px, 5vw, 48px);
  font-weight: 800;
  line-height: 1.1;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-transform: none;
  letter-spacing: -0.03em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 0 2px 10px rgba(94, 114, 228, 0.1);
}

p {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.01em;
  line-height: 1.7;
}

.sectionTitle {
  margin-bottom: var(--spacing-2xl);
  position: relative;
  font-size: clamp(28px, 4vw, 36px);
  text-align: center;
  font-weight: 700;
  letter-spacing: -0.03em;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: fadeInUp 0.6s var(--animation-smooth) both;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 0 3px 15px rgba(94, 114, 228, 0.15);
}

.sectionTitle::after {
  content: '';
  display: block;
  width: 80px;
  height: 4px;
  background: var(--gradient-accent);
  margin: var(--spacing-md) auto 0;
  border-radius: var(--border-radius-sm);
  transition: all var(--transition-duration) var(--transition-ease);
  animation: scaleIn 0.8s var(--animation-bounce) 0.3s both;
}

.hover {
  cursor: pointer;
  transition: all var(--transition-duration) var(--transition-ease);
  border-radius: var(--border-radius);
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
  backface-visibility: hidden;
}

.hover::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.hover:hover::before {
  left: 100%;
}

.hover:hover {
  transform: translateY(-8px) scale(1.03) rotateX(5deg);
  box-shadow: var(--shadow-hover);
}

.hover:focus {
  outline: 2px solid var(--btn-color);
  outline-offset: 2px;
}

/* Modern Button Design */
button {
  background: var(--gradient-primary);
  color: var(--btn-text-color);
  border: none;
  padding: var(--spacing-md) var(--spacing-xl);
  border-radius: var(--border-radius);
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-duration) var(--transition-ease);
  box-shadow: var(--shadow-sm);
  position: relative;
  overflow: hidden;
  letter-spacing: 0.025em;
  text-transform: uppercase;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  backdrop-filter: blur(10px);
}

button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

button:hover::before {
  width: 300px;
  height: 300px;
}

button:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: var(--shadow-hover);
  filter: brightness(1.15) saturate(1.2);
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(9, 135, 242, 0.2), 0 8px 25px rgba(9, 135, 242, 0.3);
}

button:active {
  transform: translateY(-1px) scale(1.02);
  box-shadow: 0 2px 10px rgba(9, 135, 242, 0.2);
}

/* Glass Card Design */
.card {
  background: var(--glass-bg);
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  border-radius: var(--border-radius-lg);
  padding: var(--spacing-xl);
  box-shadow: var(--shadow-card);
  border: 1px solid var(--glass-border);
  transition: all var(--transition-duration) var(--transition-ease);
  position: relative;
  overflow: hidden;
}

.card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transform: rotate(45deg);
  transition: all 0.6s;
  opacity: 0;
}

.card:hover::before {
  animation: shimmerCard 0.6s ease;
}

@keyframes shimmerCard {
  0% {
    transform: translateX(-100%) translateY(-100%) rotate(45deg);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translateX(100%) translateY(100%) rotate(45deg);
    opacity: 0;
  }
}

.card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: var(--shadow-hover);
  border-color: rgba(94, 114, 228, 0.3);
  background: rgba(255, 255, 255, 0.95);
}

[data-theme='dark'] .card:hover {
  background: rgba(22, 27, 34, 0.95);
  border-color: rgba(88, 166, 255, 0.3);
}

/* Grid Container with stagger animation */
.gridContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-xl);
}

.gridContainer > * {
  animation: fadeInUp 0.6s var(--animation-smooth) both;
}

.gridContainer > *:nth-child(1) { animation-delay: 0.1s; }
.gridContainer > *:nth-child(2) { animation-delay: 0.2s; }
.gridContainer > *:nth-child(3) { animation-delay: 0.3s; }
.gridContainer > *:nth-child(4) { animation-delay: 0.4s; }
.gridContainer > *:nth-child(5) { animation-delay: 0.5s; }
.gridContainer > *:nth-child(6) { animation-delay: 0.6s; }

/* Loading animation for images */
img {
  transition: opacity var(--transition-duration) ease, transform var(--transition-duration) ease;
}

img:not([src]) {
  opacity: 0;
}

img.loaded {
  animation: imageLoad 0.6s ease;
}

@keyframes imageLoad {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Improved link styles */
a {
  transition: all var(--transition-duration) ease;
  text-decoration: none;
  position: relative;
}

a:not(.socialLink)::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--gradient-primary);
  transition: width var(--transition-duration) ease;
}

a:not(.socialLink):hover::after {
  width: 100%;
}

a:focus {
  outline: 2px solid var(--btn-color);
  outline-offset: 2px;
  border-radius: var(--border-radius-sm);
}

/* Skeleton Loading */
.skeleton {
  background: linear-gradient(90deg, 
    var(--card-bg) 0%, 
    rgba(9, 135, 242, 0.1) 50%, 
    var(--card-bg) 100%);
  background-size: 1000px 100%;
  animation: shimmer 2s infinite;
  border-radius: var(--border-radius);
}

/* Scroll Animation Classes */
.animate-in {
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 0.8s var(--animation-smooth) forwards;
}

.animate-scale-in {
  opacity: 0;
  transform: scale(0.9);
  animation: scaleInUp 0.8s var(--animation-spring) forwards;
}

@keyframes scaleInUp {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.animate-slide-left {
  opacity: 0;
  transform: translateX(-50px);
  animation: slideInLeft 0.8s var(--animation-smooth) forwards;
}

.animate-slide-right {
  opacity: 0;
  transform: translateX(50px);
  animation: slideInRight 0.8s var(--animation-smooth) forwards;
}

/* Enhanced Animation Keyframes */
@keyframes fadeIn {
  from {
    opacity: 0;
    filter: blur(10px);
  }
  to {
    opacity: 1;
    filter: blur(0px);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px) scale(0.95);
    filter: blur(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
    filter: blur(0px);
  }
}

@keyframes scaleIn {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
}

@keyframes shimmer {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  25% {
    transform: translateY(-15px) rotate(1deg);
  }
  50% {
    transform: translateY(-10px) rotate(-1deg);
  }
  75% {
    transform: translateY(-5px) rotate(1deg);
  }
}

/* Responsive Design */
@media (width >= 768px) {
  #root {
    gap: 80px;
    padding: 0 32px;
  }
  
  h1 {
    font-size: 44px;
  }
  
  h2,
  h3 {
    font-size: 22px;
  }
  
  p {
    font-size: 17px;
  }

  .sectionTitle {
    margin-bottom: 50px;
    font-size: 32px;
  }
  
  .sectionTitle::after {
    width: 90px;
    height: 4px;
  }
  
  .gridContainer {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 1024px) {
  #root {
    gap: 100px;
    padding: 0 40px;
  }
  
  h1 {
    font-size: 48px;
  }
  
  h2,
  h3 {
    font-size: 24px;
  }
  
  p {
    font-size: 18px;
  }

  .sectionTitle {
    margin-bottom: 60px;
    font-size: 36px;
  }
  
  .sectionTitle::after {
    width: 100px;
    height: 4px;
  }
  
  .gridContainer {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1400px) {
  #root {
    gap: 120px;
    padding: 0 60px;
    max-width: 1400px;
  }
  
  h1 {
    font-size: 56px;
  }
  
  h2,
  h3 {
    font-size: 28px;
  }
  
  p {
    font-size: 20px;
  }

  .sectionTitle {
    margin-bottom: 75px;
    font-size: 42px;
  }
  
  .sectionTitle::after {
    width: 120px;
    height: 5px;
  }
  
  .gridContainer {
    grid-template-columns: repeat(3, 1fr);
    gap: var(--spacing-2xl);
  }
}

@media (width >= 1920px) {
  #root {
    gap: 150px;
    max-width: 1600px;
  }
  
  h1 {
    font-size: 64px;
  }
  
  .sectionTitle {
    font-size: 48px;
  }
}

/* Reduce motion for accessibility */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .hover:hover,
  .card:hover {
    transform: none;
  }
  
  button:hover {
    transform: none;
  }
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-track {
  background: var(--surface-color);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--gradient-primary);
  border-radius: 10px;
  border: 2px solid var(--surface-color);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--gradient-accent);
  box-shadow: 0 0 10px rgba(94, 114, 228, 0.3);
}

/* Selection Styles */
::selection {
  background: rgba(94, 114, 228, 0.3);
  color: var(--text-color);
  text-shadow: none;
}

::-moz-selection {
  background: rgba(94, 114, 228, 0.3);
  color: var(--text-color);
  text-shadow: none;
}